import { UIElement } from './UIElement';

export class UIWindow extends UIElement {

    static style() {
        return `
            <style>
                :host {
                    display: block;
                    position: absolute;
                    top: var(--y);
                    left: var(--x);
                    width: var(--width);
                    height: var(--height);
                    min-width: 150px;
                    min-height: 100px;
                    max-width: 100vw;
                    max-height: 100vh;
                    background: #333;
                    font-family: sans-serif;
                    color: #eee;
                    border-radius: 3px;
                    overflow: hidden;
                    animation: show .1s;
                    box-shadow: 0 0 8px rgba(0, 0, 0, 0.33);
                }

                :host([dragging]) .content {
                    pointer-events: none;
                }

                :host([dragging]) .drag-bar {
                    background: #555;
                }

                :host([closing]) {
                    animation: hide .1s;
                }

                @keyframes show {
                    from { transform: scale(0); opacity: 0; }
                }

                @keyframes hide {
                    to { transform: scale(0); opacity: 0; }
                }

                .drag-bar {
                    position: relative;
                    background: #444;
                    width: 100%;
                    height: 24px;
                    user-select: none;
                    padding: 0 8px;
                    box-sizing: border-box;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                }

                .title {
                    font-size: 12px;
                    opacity: 0.75;
                }

                .drag {
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                }

                .close {
                    position: relative;
                    z-index: 1000;
                    display: block;
                    width: 12px;
                    height: 12px;
                    border-radius: 50%;
                    background: #333;
                    transition: background .1s ease-out;
                }

                .close:hover {
                    background: red;
                }
                .close:active {
                    transition-duration: 0s;
                    background: darkred;
                }

                .content {
                    font-size: 13px;
                }

            </style>
        `;
    }

    static template(props) {
        return `
            <div class="drag-bar">
                <span class="title">${props.title}</span>
                <span class="drag"></span>
                <span class="close"></span>
            </div>
            <div class="content">
                <slot></slot>
            </div>
        `;
    }

    get visible() {
        return this.parentElement != null;
    }

    constructor(props = { uid: null, title: null }) {
        super();
        this.props = props;
        this.id = props.uid;

        this.loadLayout();

        if(this.showing) {
            this.show();
        }
    }

    connectedCallback() {
        super.connectedCallback();

        const close = this.getElement('.close');
        close.addEventListener('click', () => this.hide());

        const bar = this.getElement('.drag-bar');
        const drag = this.getElement('.drag');
        let x = 0, y = 0;
        drag.addEventListener('mousedown', e => {
            if(e.button === 0) {
                this.setAttribute('dragging', '');
                const rect = bar.getBoundingClientRect();
                x = rect.x - e.x;
                y = rect.y - e.y;
            }
        })
        window.addEventListener('mouseup', e => {
            this.removeAttribute('dragging', '');
            this.saveLayout();
            x = 0;
            y = 0;
        })
        window.addEventListener('mousemove', e => {
            if(e.x + e.y !== 0 && x && y) {
                this.x = (e.x + x);
                this.y = (e.y + y);
                this.style.setProperty('--x', this.x + "px");
                this.style.setProperty('--y', this.y + "px");
            }
        })
    }

    toggle() {
        this.visible ? this.hide() : this.show();
    }

    hide() {
        this.setAttribute('closing', '');
        function hideAnim() {
            this.removeAttribute('closing');
            this.remove();
            this.removeEventListener('animationend', hideAnim);
        }
        this.addEventListener('animationend', hideAnim);
        this.showing = false;
        this.saveLayout();
    }

    show() {
        document.body.appendChild(this);
        this.showing = true;
        this.saveLayout();
    }

    saveLayout() {
        const layout = JSON.parse(localStorage.getItem('ui-layout')) || {};
        layout[this.id] = {
            x: this.x,
            y: this.y,
            showing: this.showing
        }
        localStorage.setItem('ui-layout', JSON.stringify(layout));
    }

    loadLayout() {
        const layout = JSON.parse(localStorage.getItem('ui-layout')) || {};
        if(layout[this.id]) {
            const win = layout[this.id];
            this.x = win.x;
            this.y = win.y;
            this.showing = win.showing;

            this.style.setProperty('--x', this.x + "px");
            this.style.setProperty('--y', this.y + "px");
        }
    }

}

customElements.define('ui-window', UIWindow);
