import { UIElement } from './UIElement';

export class Menu extends UIElement {

    static style() {
        return `
            <style>
                :host {
                    position: absolute;
                    top: 10px;
                    left: 10px;
                }
            
                .menu-container {
                    background: #333;
                    border-radius: 3px;
                    padding: 4px 2px;
                }

                .menu-items {
                    display: flex;
                }

                .menu-item {
                    padding: 4px;
                    margin: 0 4px;
                    border-radius: 3px;
                    transition: .15s ease-out;
                    cursor: pointer;
                }

                .menu-item:hover {
                    background: rgba(255, 255, 255, 0.25);
                }

                .menu-item:before {
                    content: "";
                    display: block;
                    width: 25px;
                    height: 25px;
                    background: #1c1c1c;
                    border-radius: 50%;
                    border-radius: 50%;
                }
            
            </style>
        `;
    }

    static template() {
        return `
            <div class="menu-container">
                <div class="menu-items">
                </div>
            </div>
        `;
    }

    createItem({
        name = "",
        onclick = null,
    }) {
        const list = this.getElement('.menu-items');
        const item = document.createElement('div');
        item.onclick = onclick;
        item.title = name;
        item.className = "menu-item";
        list.appendChild(item);
    }

}

customElements.define('ui-menu', Menu);
